body {
    background-color: black;
}

button:hover {
    filter: brightness(0.95);
}

@media only screen and (max-width: 600px) {
    .carousel-item {
        height: 350px !important;
    }
    .meal_list {
        margin-top: 0!important;
        width: 100% !important;
    }
    .meal_list input {
        width: 100px;
        border: 1px solid;
        border-radius: 4px;
    }
    .item_desc {
        display: none;
    }
    .item_price {
        margin-bottom: 10px;
    }
    .item {
        flex-direction: column;
        text-align: center;
        font-size: 14px;
    }
    .custom_modal {
        width: 100% !important;
        left: 0px !important;
    }
    .meal_modal_footer {
        text-align: center !important;
    }
}