.cart_meal_list {
    margin: auto;
    width: 90%;
    background-color: white;
    border-radius: 10px;
    padding: 10px;
}

.cart_meal_list ul {
    list-style: none;
    padding: 0;
}

.cart_meal_list ul>li {
    border-bottom: 2px solid goldenrod;
    padding: 10px;
}

.meal_modal_footer {
    text-align: right;
}

.meal_modal_footer button {
    border: none;
    margin: 10px;
    width: 150px;
    padding: 10px;
    border-radius: 5px;
}

.meal_modal_footer button:first-child {
    background-color: lightgray;
}

.meal_modal_footer button:last-child {
    background-color: #0dcaf0;
}

.cart_btns {
    float: right;
}

.cart_btns button {
    border: none;
    height: 40px;
    width: 40px;
    margin: 10px;
    font-weight: bold;
    font-size: 25px;
    border-radius: 10px;
}