.meal_list {
    margin: auto;
    width: 70%;
    background-color: white;
    border-radius: 10px;
    padding: 20px;
    margin-top: -100px;
    position: relative;
    z-index: 5;
}

.meal_list ul {
    list-style: none;
    padding: 0;
}

.meal_list ul>li {
    border-bottom: 2px solid goldenrod;
    padding: 10px;
}

.meal_container {
    height: 235px;
    overflow-y: auto;
}

.item {
    display: flex;
}

.item>div:first-child,
div:nth-child(2) {
    flex: 1;
}

.item_title {
    color: teal
}

.item_desc {
    font-style: italic;
}

.item_price {
    color: darkblue;
    font-size: 30px;
    font-weight: bold;
}

.amount_input {
    width: 60px;
    height: 40px;
    margin: 10px;
}

.amount_add_btn {
    background-color: lightgreen;
    border: none;
    border-radius: 5px;
    margin-top: 5px;
    width: 150px;
    height: 40px;
}